import React, { Component } from 'react'

import anchorme from 'anchorme'

// import FormatDate from '../helper/FormatDate'
import formatEmotes from '../helper/formatEmotes'
import getRandomColor from '../helper/getRandomColor'

// eslint-disable-next-line no-control-regex
const ACTION = /(\x01ACTION )(.*)(\x01)/

class App extends Component {
  constructor (props) {
    super(props)
    // console.log(props)

    this.bagdes = props.bagdes

    this.bttvBadges = props.bttvBadges
    this.ffzBadges = props.ffzBadges

    this.chan = props.source.chan
    this.msg = props.source.msg
    this.name = props.source.name
    this.tags = props.source.tags
    this.timestamp = props.source.timestamp

    if (this.msg && this.msg.toLowerCase().indexOf('@' + this.chan) !== -1) {
      this.styleColor = { color: 'red' }
    }

    const found = ACTION.exec(this.msg)
    if (found !== null) {
      this.msg = found[2]
      this.styleColor = { color: this.tags.color }
    }

    if (this.tags && this.tags.banDuration) { // ban-duration (Optional) Duration of the timeout, in seconds. If omitted, the ban is permanent.
      this.styleColor = {
        textDecoration: 'line-through',
        textDecorationColor: 'rgba(255, 193, 7, 0.8)'
      }
    } else if (this.tags && this.tags.banReason) {
      this.styleColor = {
        textDecoration: 'line-through',
        textDecorationColor: 'rgba(255, 0, 0, 0.8)'
      }
    }

    // this._msg = this.msg
    if (this.tags && this.tags.emotes) {
      this.msg = formatEmotes({
        msg: this.msg,
        emotes: this.tags.emotes,
        measure: props.measure,
        id: this.tags.id,
        badges: this.bagdes,
        bttvBadges: this.bttvBadges,
        ffzBadges: this.ffzBadges,
        chan: this.chan
      })
    }

    // globalbttv TODO
    // this._msg = findBttvEmote({ msg: this._msg, chan: this.chan, badges: this.bagdes, measure: props.measure })

    // console.log(this.msg)
    // console.log(this._msg)

    if (this.tags && this.tags.color === true) {
      this.tags.color = getRandomColor()
    }

    if (props.detectLinks) {
      this.msg = anchorme(this.msg, {
        truncate: [30, 15],
        attributes: [{ name: 'target', value: '_blank' }]
      })
    }
  }

  render () {
    let banText
    if (this.tags && this.tags.banMsg) {
      banText = <span style={{ fontWeight: 'bold' }} className='line'>{this.tags.banMsg}</span>
    }

    return (
     <><span style={this.styleColor}>{this.msg}</span>{banText}</>
    )
  }
}

export default App
