/* globals fetch:true */

import { connect } from 'react-redux'
import { update } from '../redux'

import difference from 'lodash.difference'

let tempChannels

export const _connect = ({ update, badges, channels }) => {
  // console.log('badges_connect', badges, channels)

  const StoreChannel = channels.toArray().filter(chan => chan.roomId) // filtere undefined roomId raus

  const ChannelState = tempChannels
  // console.log({ StoreChannel, ChannelState })

  const _join = difference(StoreChannel, ChannelState)
  if (_join.length) {
    // console.log('badges_connectjoin', _join)
  }

  const _part = difference(ChannelState, StoreChannel)
  if (_part.length) {
    // console.log('badges_connectpart', _part)
    // https://badges.twitch.tv/v1/badges/channels/gronkh/display
  }

  if (badges.global.size === 0) {
    fetch('https://badges.twitch.tv/v1/badges/global/display')
      .then(res => {
        if (res.status !== 200) throw new Error(res.status || res.statusText)
        else return res.json()
      })
      .then(res => update({ key: 'global', value: res.badge_sets }, 'UPDATE_BADGES'))
      .catch(err => { console.warn(err) })
      // .then(res => {
      //   update({ key: 'global', value: res.badge_sets }, 'UPDATE_BADGES')
      // })

    fetch('https://api.betterttv.net/2/emotes')
      .then(res => {
        if (res.status !== 200) throw new Error(res.status || res.statusText)
        else return res.json()
      })
      .then(res => {
        const emotes = {}
        for (let i = 0, len = res.emotes.length; i < len; i++) {
          emotes[res.emotes[i].code] = res.emotes[i]
        }
        update({ key: 'globalbttv', value: emotes }, 'UPDATE_BADGES')
      })
      .catch(err => { console.warn(err) })

    // https://api.betterttv.net/2/emotes 2.0k
    // https://api.betterttv.net/2/channels/:name 1.6k

    // https://community.nightdev.com/t/bttv-api-for-tracking-usage-of-own-emotes/20922
    // https://community.nightdev.com/t/is-there-a-bettertwitchtv-api/5223

    // https://github.com/BatedUrGonnaDie/glitchify/blob/master/app/src/main/java/com/leagueofnewbs/glitchify/Main.java#L427

    fetch('https://api.frankerfacez.com/v1/set/global')
      .then(res => {
        if (res.status !== 200) throw new Error(res.status || res.statusText)
        else return res.json()
      })
      .then(res => {
        const resEmotes = res.sets[res.default_sets[0]].emoticons
        const emotes = {}
        for (let i = 0, len = resEmotes.length; i < len; i++) {
          emotes[resEmotes[i].name] = resEmotes[i]
        }
        update({ key: 'globalffz', value: emotes }, 'UPDATE_BADGES')
      })
      .catch(err => { console.warn(err) })
  }

  StoreChannel.forEach(value => {
    if (!badges[value.name]) {
      fetch(`https://badges.twitch.tv/v1/badges/channels/${value.roomId}/display`)
        .then(res => {
          if (res.status !== 200) throw new Error(res.status || res.statusText)
          else return res.json()
        })
        .then(res => {
          update({ key: value.name, value: res.badge_sets }, 'UPDATE_BADGES')
        })
        .catch(err => { console.warn(err) })
    }

    if (!badges[`${value.name}-bttv`]) {
      fetch(`https://api.betterttv.net/2/channels/${value.name}`)
        .then(res => {
          if (res.status !== 200) throw new Error(res.status || res.statusText)
          else return res.json()
        })
        .then(res => {
          const emotes = {}
          for (let i = 0, len = res.emotes.length; i < len; i++) {
            emotes[res.emotes[i].code] = res.emotes[i]
          }
          update({ key: `${value.name}-bttv`, value: emotes }, 'UPDATE_BADGES')
        })
        .catch(err => {
          if (err.message !== '404') {
            console.warn(err.message)
          }
        })
    }

    if (!badges[`${value.name}-ffz`]) {
      fetch(`https://api.frankerfacez.com/v1/room/id/${value.roomId}`)
        .then(res => {
          if (res.status !== 200) throw new Error(res.status || res.statusText)
          else return res.json()
        })
        .then(res => {
          if (res.room && res.room.set) {
            if (res.sets[res.room.set] && res.sets[res.room.set].emoticons && res.sets[res.room.set].emoticons.length !== 0) { // das Array k√∂nnte auch leer sein
              const resEmotes = res.sets[res.room.set].emoticons
              const emotes = {}
              for (let i = 0, len = resEmotes.length; i < len; i++) {
                emotes[resEmotes[i].name] = resEmotes[i]
              }
              update({ key: `${value.name}-ffz`, value: emotes }, 'UPDATE_BADGES')
            }
          }
        })
        .catch(err => {
          if (err.message !== '404') {
            console.warn(err.message)
          }
        })
    }
  })

  tempChannels = StoreChannel
  return null
}

export default connect(
  state => ({
    channels: state.channels,
    badges: state.badges
  }), // mapStateToProps
  { update } // mapDispatchToProps
)(_connect)
